<template>
    <v-btn
      :loading='loadingState'
      :disabled='loadingState || disabled'
      @click="clickHandler"
      class="departamento-button"
      elevation="0"
    >
      <slot>
        Відправити
      </slot>
    </v-btn>
</template>
<script>
export default {
  name: 'Button',
  props: {
    loadingState: {
      required: false,
      type: Boolean
    },
    disabled: {
      type: Boolean,
    }
  },
  methods: {
    clickHandler(){
      this.$emit('custom-click')
    }
  }
}
</script>