<template>
    <div class="indiv-plan__table-row">
      <div class="indiv-plan__table-cell__wrapper">
        <custom-table-unique-cell
            v-if="tableSlotProps.column.children"
            :tableProps="tableSlotProps"
            @cell-click="showCellInput"
            @input-enter="hideCellInput"
        ></custom-table-unique-cell>

        <template v-else>
          <div class="indiv-plan__table-cell" @click="showCellInput(cell)">
            <tr>
              <td
                class="indiv-plan__table-cell__input" 
                v-if="cell.isEdited"
              >
                <formulate-input
                  type="text"
                  v-model="cell.value"
                  @keydown.enter="hideCellInput(cell)"
                ></formulate-input>
              </td>
              <td class="indiv-plan__table-cell__data" v-else>
                {{ cell.value }}
              </td>
            </tr>
          </div>
        </template>
      </div>
    </div>
</template>
<script>
import CustomTableUniqueCell from './CustomTableUniqueCell.vue';

export default {
  name: 'CustomTableCell',
  props: {
    tableSlotProps: {
      type: Object,
    }
  },
  components: {
    CustomTableUniqueCell,
  },
  methods: {
    showCellInput(cell) {
      cell.isEdited = true;
    },
    hideCellInput(cell) {
      cell.isEdited = false;
    },
  },
  computed: {
    cell() {
      return this.tableSlotProps.row[this.tableSlotProps.column.field];
    }
  }
}
</script>