<template>
  <div class="doc__add-row">
    <div class="doc__add-row__inner">
      <div class="doc__add-row__form">
        <FormulateForm
          class="doc__add-row__formulate-form"
          #default="{ isValid }"
          :schema="schema"
          @input="handleChanges"
        >
          <div class="doc__add-row__form--actions">
            <base-button
              class="doc__add-row__button"
              @custom-click="addRow"
              :disabled="!isValid"
            >
              Додати
              <v-icon
                size="24"
                color="white"
                class="doc__add-row__button-icon"
              >
                mdi-plus-circle
              </v-icon>
            </base-button>
          </div>
        </FormulateForm>
      </div>
    </div>
  </div>
</template>
<script>
import BaseButton from '@/components/BaseComponents/BaseButton';

export default {
  name: 'AddRow',
  props: {
    schema: Array,
  },
  data() {
    return {
      form: null,
    }
  },
  components: {
    BaseButton,
  },
  methods: {
    addRow() {
      this.$emit('add-row', Object.assign({}, this.form));
      this.$formulate.reset('doc__add-row__formulate-form');
    },
    handleChanges(formObject) {
      this.form = formObject;
    }
  },
}
</script>