<template>
    <div class="indiv-plan__cell--unique">
      <tr
        class="indiv-plan__table-cell--unique__subcolumn" 
      >
        <template
          v-for="subcolumn of tableProps.column.children"
        >
          <div 
            class="indiv-plan__table-cell--unique__wrapper"
            :key="subcolumn.label" 
            @click="handleCellClick(tableProps.row[subcolumn.field])"
          >
            <td
              class="indiv-plan__table-cell--unique__input" 
              v-if="tableProps.row[subcolumn.field].isEdited"
            >
              <formulate-input
                type="text"
                v-model="tableProps.row[subcolumn.field].value"
                @keydown.enter="handleEnterKeydown(tableProps.row[subcolumn.field])"
              ></formulate-input>
            </td>
            <td 
              class="indiv-plan__table-cell__data" 
              v-else
            >
              {{ tableProps.row[subcolumn.field].value }}
            </td>
          </div>
        </template>
      </tr>
  </div>
</template>
<script>
export default {
  name: 'UniqueCell',
  props: {
    tableProps: {
      required: true,
    }
  },
  data() {
    return {
      isFirstSubcolumnEdited: false,
      isSecondSubcolumnEdited: false,
    }
  },
  methods: {
    handleCellClick(cell) {
      this.$emit('cell-click', cell)
    },
    handleEnterKeydown(cell) {
      this.$emit('input-enter', cell)
    },
  },
}
</script>