<template>
  <div class="departamento-docs__new-individual-plan">
    <tabs
      :tabs="tabs"
      :activeTab="docStep"
      class="departamento-docs__new-individual-plan__tabs"
      @tab-click="handleTabSelection"
    ></tabs>
    <transition name="fade-out" mode="out-in">
      <keep-alive>
        <base-doc-form-title
          title="Індивідуальний план"
          subtitle="Роботи викладача та її облік"
          v-if="docStep === 'doc-form-title'"

          @form-title-change="handleTitleFormChanges"
        ></base-doc-form-title>
        
        <base-doc-form-template
          v-if="docStep === 'doc-form-science'"

          @add-row="addRow"

          :schema="schema"
          :columns="columns"
          :rows="rows"
        >
          <template #document-header>
            <div class="departamento__doc-inputs">
              <add-row
                :schema="schema"
                @add-row="addRow"
              ></add-row>
            </div>
          </template>

          <template #document-body>
            <vue-good-table
              :columns="columns"
              :rows="rows"
              :sort-options="{ enabled: false }"
            >
              <template slot="table-column" slot-scope="props">
                <div class="indiv-plan__table-col-header--unique" v-if="isColHeaderSpecialCase(props.column.label)">
                  <div class="indiv-plan__col-heading">
                    <p>
                      {{ props.column.label }}
                    </p>
                  </div>
                  <div class="indiv-plan__col-subheading">
                    <tr class="indiv-plan__col-subheading--row">
                      <th>пл.</th>
                      <th>вк.</th>
                    </tr>
                  </div>
                </div>
                <div class="indiv-plan__table-col-header indiv-plan__col-heading" v-else>
                  <span>
                    {{ props.column.label }}
                  </span>
                </div>
              </template>

              <template slot="table-row" slot-scope="props">
                  <custom-table-cell
                    :tableSlotProps="props"
                  ></custom-table-cell>
              </template>

              <template slot="emptystate">
                <p class="table__empty-text">
                  Даних немає!
                </p>
              </template>
            </vue-good-table>
          </template>
        </base-doc-form-template>
      </keep-alive>
    </transition>
  </div>
</template>
<script>
import { VueGoodTable } from 'vue-good-table';
import 'vue-good-table/dist/vue-good-table.css';

import { mapState, mapMutations } from 'vuex';

import schema from '@/form-schemas/indiv-plan-schema';

import Tabs from '@/elements/Tabs';
import AddRow from '@/elements/AddRow';
import CustomTableCell from '@/elements/CustomTableCell';

import BaseDocFormTitle from '@/components/Docs/BaseDocFormTitle';
import BaseDocFormTemplate from '@/components/Docs/BaseDocFormTemplate';

export default {
  name: 'NewIndividualPlan',
  components: {
    BaseDocFormTitle,
    BaseDocFormTemplate,
    Tabs,
    VueGoodTable,
    AddRow,
    CustomTableCell
  },
  data() {
    return {
      schema: schema,
      tabs: [
        {
          componentName: 'doc-form-title',
          name: 'Титульний лист'
        },
        {
          componentName: 'doc-form-science',
          name: 'Навчальна робота'
        },
      ],
      docStep: 'doc-form-title',
      indivPlanForm: {
        titleForm: {},
        bodyForm: {}
      }
    }
  },
  methods: {
    handleTabSelection(tabName) {
      this.docStep = tabName;
    },
    addRow(data) {
      console.log('data row', data );
      this.commit('addRow', data);
    },
    isColHeaderSpecialCase(headerColLabel) {
      return this.individualPlan.uniqueColumns.includes(headerColLabel.toLowerCase())
    },
    ...mapMutations({
      addRow: 'individualPlan/addRow',
    }),
    handleTitleFormChanges(form) {
      this.indivPlanForm.titleForm = form;
    }
  },
  computed: {
    ...mapState([
      'individualPlan',
    ]),
    columns() {
      return this.individualPlan.columns;
    },
    rows() {
      return this.individualPlan.rows;
    },
  }
}
</script>
<style lang="scss">
    .departamento__doc {
    padding-top: 30px;

    &-inputs {
      padding-bottom: 30px;
    }
  }
</style>